


/*======================
  1.General CSS start
========================*/
body{
  font-family:$font-1,$font-2;
  overflow-x:hidden;
  //display:none;
  -webkit-font-smoothing:antialiased;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  display: inline-block;
}
h2{
  margin-bottom: 0;
  font-weight:700;
  font-family:$font-1;
  line-height:1;
}
h3{
  margin-bottom: 0;
}
h4{
  margin-bottom: 0;
}
h5{
  margin-bottom: 0;
}
p{
  font-weight: 400;
  font-family: $font-2;
  margin-bottom: 0;
}
a{
  transition: 0.5s ease;
  text-decoration: none;
  &:hover {
    transition: 0.5s ease;
    text-decoration: none;
  }
  &:focus {
    outline: none;
  }
}


.title-1{
  text-align:center;
  h2{
    //font-size:calc(25px + (62 - 25) * ((100vw - 320px) / (1920 - 320)));
    font-size:62px;
    color:$title-color;
  }
  p{
    //font-size:calc(14px + (28 - 14) * ((100vw - 320px) / (1920 - 320)));
    font-size:28px;
    color:$dark;
    font-weight:400;
    font-family:$font-2;
  }
  &.white-title{
    h2{
      color:$white;
    }
    p{
      color:$white;
    }
  }
}
.theme-color1{
  background-color:$theme-color1;
}
.theme-color2{
  background-color:$theme-color2;
}
.color1{
  background-color: $green;
}
.color2{
  background-color: $blue;
}
.color3{
  background-color: $orange;
}
.color4{
  background-color: $pink;
}


.section-pt-70{
  padding-top:70px;
}

//.shadow1{
