//}
//.shadow2{
//  filter: drop-shadow(20px 20px 0px $blue);
//}
//.shadow3{
//  filter: drop-shadow(20px 20px 0px $drop-shadow2);
//}

.arrow-1{
  .slick-prev,.slick-next{
    top:48%;
    width:auto;
    height:auto;
    &:before{
      color:$white;
      font-family:$font-awesome;
      font-weight:400;
      font-size:70px;
      opacity:1;
      transition: all 0.5s ease;
    }
  }
  .slick-prev{
    left: -50px;
    &:before{
      content:"\f104";
    }

  }
  .slick-next{
    right: -50px;
    &:before{
      content:"\f105";
    }
  }
  &.arrow-dark{
    .slick-prev,.slick-next{
      &:before{
        color:$dark1;
      }
      &:hover{
        &:before{
          color: $theme-color2;
        }
      }
    }
  }
}



// other browsers css //

[class*="col-"] {
