/*============
 1.header css
==============*/
header{
  text-align:center;
  position:absolute;
  top:0;
  right:0;
  width:100%;
  padding-top:65px;
  z-index:3;
  .about-link{
    position:absolute;
    top:0;
    bottom:0;
    right:100px;
