/*=================
  2.Baby genius css
===================*/
.baby-genius{
  background-size:cover;
  background-position:center;
  position:relative;
  //z-index: 2;
  overflow: hidden;
  &:before{
    position:absolute;
    content:'';
    top:99.9%;
    left:0;
    height:146px;
    width:100%;
    //background-repeat:no-repeat;
    background:url("../images/baby-genius/before.png");
    background-position:center;
    display: none;
  }
  &:after{
    position:absolute;
    content:'';
    top:100%;
    left:0;
    width:100%;
    height:310px;
    background-color:$theme-color2;
    z-index:-1;
    background-position:center;
    display:none;
  }
  .baby-genius-contain{
    display: flex;
    align-items: center;
    height:1135px;
    position: relative;
    >div{
      width:32.5%;
    }
    h4{
      font-size:29px;
      color:$white;
      font-weight:400;
      font-family:$font-2;
      span{
        color:$orange;
        font-weight:900;
      }
    }
    h2{
      font-size:61px;
      color:$white;
      text-transform:capitalize;
      margin-bottom:25px;
    }
    p{
      font-size:25px;
      color:$white;
      span{
        color:$orange;
        font-weight:500;
      }
    }
  }
  .icon-block{
    position:absolute;
    bottom:14%;
    li{
      &:nth-child(n+2){
        margin-left:15px;
      }
    }
  }
  .partner-animat{
    position:absolute;
    bottom:19%;
    right:0;
    width:50%;
  }
  .animat-block{
    .cloud1,.cloud2,.cloud3{
      opacity:0.5;
    }
    .cloud1,.cloud2,.cloud3,.star1,.star2,.star3{
      position:absolute;
    }

    .cloud1{
      right:0;
      bottom:13%;
      animation: cloudanimat 8s linear infinite;
    }
    .cloud2{
      top:25%;
      right:5%;
      animation: cloudanimat 5s linear infinite;
    }
    .cloud3{
      left:-70px;
      bottom:25%;
      animation: cloudanimat 10s linear infinite;
    }
    .star1{
      bottom:25%;
      left:37%;
      animation: hom-round 10s linear infinite;
    }
    .star2{
      top: 32%;
      right: 49%;
      animation: hom-round 5s linear infinite;
    }
    .star3{
      top: 8%;
      right: 25%;
      animation: hom-round 6s linear infinite;
    }
  }
}


/*=======================
  2.Right Stimulation
========================*/
.stimulation{
  position:relative;
  padding-top:119px;
  padding-bottom:20px;
  background-size:cover;
  z-index:1;
  &:before{
    position:absolute;
    content:'';
    top:99.9%;
    left:0;
    height:233px;
    width:100%;
    background-image:url("../images/stimulation/before.png");
    background-position:center;
    background-repeat:no-repeat;
  }
  .title-1{
    margin-bottom:62px;
    h2{
      margin-bottom:20px;
    }
    p{
      margin-left:auto;
      margin-right:auto;
    }
  }
  .stimulation-contain{
    ul.stimulate-main{
      display: grid;
      align-items: center;
      width:fit-content;
      li{
        display:flex;
        display: -webkit-box;
        align-items:center;
        justify-content:center;
        position: relative;
        .img-wrraper{
          width:35%;
          text-align:center;
        }
        .content-wrraper{
          width:50%;
          h3{
            font-size: 46px;
            color: $white;
            font-weight:700;
            font-family:$font-1;
            margin-bottom:5px;
          }
          p{
            font-size:25px;
            color:$dark;
          }
        }
        &:nth-child(odd){
          .img-wrraper{
            text-align:right;
            margin-right:70px;
          }
        }
        &:nth-child(even){
          .img-wrraper{
            text-align:left;
            margin-left:70px;
          }
          .content-wrraper{
            text-align:right;
            p{
              margin-left: auto;
            }
          }
        }
      }
    }
  }
  .animat-block{
    .animat1,.animat2,.animat3,.animat4{
      position: absolute;
    }
    .animat1{
      top:0;
      left:5%;
      animation:move2 12s linear infinite;
    }
    .animat2{
      top:38%;
      left:5%;
      animation:move2 9s linear infinite;
      z-index:-1;
    }
    .animat3{
      bottom: 0;
      left: 13%;
      animation: plane-key 8s linear infinite;
      z-index:-1;
    }
    .animat4{
      top:50%;
      right:10%;
      animation:move3 35s linear infinite alternate-reverse;
      z-index:-1;
    }
  }
}

