/*===============
  8.Our team
===============*/
.our-team{
  padding-top:125px;
  padding-bottom:280px;
  position:relative;
  z-index: 1;
  &:after{
    position:absolute;
    content:'';
    top:0;
    left:0;
    width:100%;
    height:480px;
    background-color:$theme-color1;
    z-index:-2;
  }
  &:before{
    position:absolute;
    content:'';
    top:479px;
    left:0;
    width:100%;
    height:227px;
    background-image: url("../images/team/before.png");
    z-index:-2;
    background-repeat:no-repeat;
    background-position: center;
  }
  .title-1{
    margin-bottom:123px;
    h2{
      margin-bottom:17px;
    }
    p{
      width:915px;
      margin-left:auto;
      margin-right:auto;
      font-size:25px;
    }
  }
  .team-box {
    height:545px;
    position: relative;
    margin-left: 22px;
    margin-right: 22px;
    .front {
      height: inherit;
      position: absolute;
      top: 0;
      z-index: 900;
      text-align: center;
      -webkit-transform: rotateX(0deg) rotateY(0deg);
      -moz-transform: rotateX(0deg) rotateY(0deg);
      -webkit-transform-style: preserve-3d;
      -moz-transform-style: preserve-3d;
      -webkit-backface-visibility: hidden;
      -moz-backface-visibility: hidden;
      transition: all .4s ease-in-out;
      &.hover{
        z-index: 900;
        transform: rotateY(180deg);
        -webkit-transform: rotateY(180deg);
        -moz-transform: rotateY(180deg);
      }
    }
    .back {
      height: inherit;
      position: absolute;
      top: 0;
      z-index: 1000;
      -webkit-transform: rotateY(-180deg);
      -moz-transform: rotateY(-180deg);
      -webkit-transform-style: preserve-3d;
      -moz-transform-style: preserve-3d;
      -webkit-backface-visibility: hidden;
      -moz-backface-visibility: hidden;
      transition: all .4s ease-in-out;
      &.hover{
        z-index: 1000;
        transform: rotateX(0deg) rotateY(0deg);
        -webkit-transform: rotateX(0deg) rotateY(0deg);
        -moz-transform: rotateX(0deg) rotateY(0deg);
      }
    }
    .front,.back{
      border-radius: 68px;
      text-align:center;
      overflow:hidden;
      perspective:1000px;
      width: 100%;
      .team-detail {
        padding-top:45px;
        padding-left:45px;
        padding-right:45px;
      }

      h3{
        font-size:35px;
        color:$title-color;
        font-weight:700;
        font-family:$font-1;
        line-height:1;
      }
      h4,h5{
        font-size:22px;
        font-weight: 400;
        font-family: $font-2;
        color: $white;
      }
      h5{
        margin-bottom:2px;
      }
      h4{
        padding-bottom:30px;
      }
      p{
        font-size: 20px;
        color: $white;
      }
      .img-wrraper{
        display: flex;
        justify-content: center;
        width:100%;
        height: 100%;
        background-position: top;
        background-size: cover;
        //background-size:contain;
      }
    }
    //&:hover{
    //  .front{
    //    z-index: 900;
    //    transform: rotateY(180deg);
    //    -webkit-transform: rotateY(180deg);
    //    -moz-transform: rotateY(180deg);
    //  }
    //  .back {
    //    z-index: 1000;
