/*===============
  9.Advisory
===============*/
.mb-35px{
  //margin-bottom:35px;
}
.advisory{
  padding-top:93px;
  padding-bottom:66px;
  position:relative;
  z-index: 1;
  .title-1{
    margin-bottom:130px;
  }
  &:before{
    position:absolute;
    content:'';
    bottom:99.8%;
    left: 0;
    height:217px;
    width:100% ;
    background-image: url("../images/advisory/before.png");
    background-repeat: no-repeat;
    background-position:center;
  }
  &:after{
    position: absolute;
    content: '';
    top: 99.8%;
    left: 0;
    height:179px;
    width: 100%;
    background-image: url("../images/advisory/after.png");
    background-repeat: no-repeat;
    background-position:center;
  }
  .advisory-box{
    width:80%;
    margin:auto;
    .shap-wrraper1,.shap-wrraper2,.shap-wrraper3,.shap-wrraper4{
      overflow: hidden;
    }
    .shap-wrraper1{
      width: 385px;
      height: 317px;
      border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
      background-color:$drop-shadow1;
    }
    .shap-wrraper2{
      width: 385px;
      height: 316px;
      border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
      background-color:$blue;
    }
    .shap-wrraper3{
      width: 385px;
      height: 303px;
      border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
      background-color:$drop-shadow2;
    }
    .advisory-detail{
      margin-top:35px;
      padding-left:20px;
      h2{
        font-size:32px;
        color: $title-color;
        margin-bottom:3px;
      }
      h3{
        font-size:20px;
        color:$dark;
        font-weight:400;
        font-family:$font-2;
      }
      .hover-contain{
        transition: all 0.5s ease;
        p{
          font-size:18px;
          color: $dark;
          font-weight: 400;
          line-height:1.4;
          font-family:$font-2;
          margin-top:25px;
        }
      }
    }
  }
  .advisory-block{
    >div{
      margin-bottom:-90px;
      .advisory-box{
        margin-bottom:90px;
      }
    }
  }
  .animat-block{
    .animat1,.animat2,.animat3,.animat4{
      position:absolute;
      z-index: -1;
      width:calc(10px + (25 - 10) * ((100vw - 320px) / (1920 - 320)));
    }
    .animat1{
      top:8%;
      right:15%;
      animation: hom-round 6s linear infinite;
    }
    .animat2{
      top: 51%;
      left: 36%;
      animation: hom-round 9s linear infinite;
    }
    .animat3{
      top:-100px;
      right:15%;
      animation:move2 12s linear infinite;
      z-index:-1;
      width:calc(25px + (84 - 25) * ((100vw - 320px) / (1920 - 320)));
    }
    .animat4{
      bottom:2%;
      left:3%;
      animation: hom-round 11s linear infinite;
    }
  }
}




/*===============
  10.Advisory
===============*/
.condition-section {
  p {
    width: unset;
  }
  .terms-details {
    ul {
      padding-left: 40px;
      list-style-type: decimal;
      li {
        display: list-item;
        font-size: 28px;
        line-height: 36px;
        color: $dark;
        font-weight: 400;
        font-family: "Lato", sans-serif;
        > ul {
          padding-top: 15px;
          list-style-type: lower-roman;
        }
        + li {
          margin-top: 20px;
        }
        span {
          display: block;
          padding-left: 15px;
        }
      }
    }
    .list-style-none {
      list-style-type: none;
      li {
        position: relative;
        &::before {
          position: absolute;
          content: '-';
          left: -20px;
          top: 0;
        }
      }
    }
    + .terms-details {
      padding-top: calc(40px + (80 - 40) * ((100vw - 320px) / (1920 - 320)));
    }
  }
}


